.menu {
  box-sizing: border-box;
  height: 100vh;
  background-color: $menu-background-color;
  font-weight: 700;
  text-transform: uppercase;

  &__top {
    margin-bottom: 32px;
  }

  &__nav {
    margin-bottom: 40px;
  }

  &__content {
    padding-inline: 20px;
  }

  &__phone-number {
    display: block;
    margin-bottom: 16px;
    font-size: 16px;
    color: inherit;
    text-decoration: none;
    font-weight: 700;
    line-height: 22px;
    letter-spacing: 2px;
    text-align: left;

  }

  &__call-to-order {
    display: block;
    width: fit-content;
    padding-bottom: 7px;
    border-bottom: 1px solid $main-text-color;

    font-size: 16px;
    font-weight: 700;
    line-height: 22px;
    letter-spacing: 2px;
    text-align: left;
    color:inherit;
    text-decoration: none;

  }

}
